.expenses-filter {
    color: white;
    padding: 0 0.9rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    /* margin-bottom: 0.5rem; */
    font-size: 1rem;
  }
  

  .expenses-filter select {
    font: inherit;
    padding: 0.5rem;
    font-weight: bold;
    border-radius: 6px;
    /* margin-bottom: 0.5rem; */

  }

  @media (max-width: 300px){
    .expenses-filter label{
      font-size: 0.6rem;
    }
  }