.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    background-color: #4b4b4b;
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    padding: 0.5rem;
    /* margin: 0 1rem; */
    color: white;
  }
  
  .expense-item__price {
    display: flex;
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #39A7FF;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    justify-content: flex-end;
  }

  .strippedDateDiv {
    display: none;
  }

  .title{
    margin: 0;
  }

  @media (max-width: 580px){
    .title{
      font-size: 0.9rem !important;
      text-align: left;
    }

    .dateCol{
      display: none;
    }

    .strippedDateDiv{
      display: block;
    }

  
  
  }
  @media (max-width: 300px){
    .expense-item__description{
      justify-content: flex-start;
      align-items: flex-start;
    }

    .expense-item__price{
      background: none;
      border: none;
      font-size: 0.8rem;
    }
  }
